import React from "react";

import Todo from "./Todo";

import './Todo.css';

const Todos = ({ todos }) => {
  return (
    <div className="todosList">
      {todos.length > 0 ? (
        todos.map((todo) => (
          <Todo key={todo.id} todoText={todo.text} todoID={todo.id} />
        ))
      ) : (
        <div className="todo">
          <p>No Todo`s Yet Bro !</p>
        </div>
      )}
    </div>
  );
};

export default Todos;
