import React, { Component, createRef } from "react";
import todoContext from "../../context/todoContext";

import "./Todo.css";

class Todo extends Component {
  state = {
    isDone: false,
  };

  static contextType = todoContext;

  changeState = () => {
    this.setState({
      isDone: !this.state.isDone,
    });
  };

  render() {
    return (
      <div className="todo">
        <div className="todo-text-info">
          <div className={`todo-line ${this.state.isDone ? "bg-success" : "bg-warning"}`} ref={this.todoLineColor}></div>
          <p className={this.state.isDone ? "text-overline" : ""}>
            {this.props.todoText}
          </p>
        </div>
        <div className="todo-action">
          <button
            onClick={this.changeState}
            className="btn btn-outline-success border-0 me-1"
          >
            <i className="fa fa-check h4"></i>
          </button>
          <button
            className="btn btn-outline-danger border-0"
            onClick={() => this.context.handleDeleteTodo(this.props.todoID)}
          >
            <i className="fa fa-trash h4"></i>
          </button>
        </div>
      </div>
    );
  }
}

export default Todo;
