import React, { Fragment } from "react";

import { Link, withRouter, NavLink } from "react-router-dom";

import './Navbar.css';

const Navbar = (props) => {
  return (
    <Fragment>
      <div className="m-3">
        <ul className="nav nav-pils justify-content-center">
          <li className="nav-item me-2">
            {/* <Link to="/" className={props.location.pathname == "/" ? "nav-link active" : "nav-link text-white"}> */}
            <NavLink
              to="/"
              exact
              className="nav-link text-white border"
              activeClassName="active"
            >
              Todo`s
            </NavLink>
          </li>
          <li className="nav-item">
            {/* <Link to="/about" className={props.location.pathname == "/about" ? "nav-link active" : "nav-link text-white"}> */}
            <NavLink
              to="/about"
              exact
              className="nav-link text-white border"
              activeClassName="active"
            >
              About
            </NavLink>
          </li>
        </ul>
      </div>
    </Fragment>
  );
};

export default withRouter(Navbar);
