import React, {useContext, useEffect, useRef} from "react";

import todoContext from '../../context/todoContext';


const AddTodo = () => {
    
  const inputFocus = useRef(null);

  useEffect(() => {
    inputFocus.current.focus();
  });

    const context = useContext(todoContext);
  
    return (
      <form className="form-add" onSubmit={event => event.preventDefault()}>
      <input
        ref={inputFocus}
        className="add-todo"
        type="text"
        placeholder="New Todo"
        onChange={context.handleGetTodoText}
        value={context.state.todo}
      />
      <button type="submit" className="btn-addTodo" onClick={context.handleAddTodo}>
        <i className="fa fa-plus-circle"></i>
      </button>
      </form>

      /* <ToastContainer /> */
  );
};

export default AddTodo;
